@use 'base/variables' as v;
@use 'base/mixins' as m;


.single{

    &__contenedor{
        overflow-y: scroll;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        @include m.tablet{
            flex-direction: row;
            height: 88vh;
            gap: 0.5rem;
        }
        @include m.xl_desktop{
            flex-direction: row;
            height: 88vh;
            gap: 0.5rem;
        }
    }

    &__contenedor::-webkit-scrollbar {
        display: none;
    }
    

    &__contenedor .slider-container{
        height: 45vh;
        @include m.tablet{
            height: 88vh;
        }
    }


    &__contenedor .slider-container img{
        object-fit: cover;
        @include m.tablet{
            object-fit: contain ;
        }
    }

    &__texto{
        width: 100%;
        margin-right: 1%;
        padding: 0 2% 0 2%;
        overflow-y: scroll;
        @include m.tablet{
            padding: 0;
        }
    }

    &__texto h2{
        margin-top: 0.5rem;
    }

    &__texto::-webkit-scrollbar {
        display: none;
    }
    


    &__slider{
        width: 100%;
        @include m.tablet{
            width: 90%;
        }
    }

    &__descripcion{
        line-height: 1.5;
        text-align: left;
        margin-bottom: 0.5rem;
        font-weight: 900;
        font-size: 1.3rem;
        @include m.xl_desktop{
            font-size: 1.4rem;
        }
    }



    &__links{
        border-top: 2px solid black;
        display: flex;
        justify-content: space-between;
        border-bottom: 2px solid black;
        padding: 4px 0;
    }

    &__links a:hover{
        color: red;
    }

    &__links a{
        color: black;
        margin-left: -2%;
    }

    &__links a:first-of-type{
        margin-left: 2%;
    }

    &__links a:last-of-type{
        margin-right: 2%;
    }

    &__tags{
        line-height: 0rem;
        padding: 2px 0;
        gap: 15px;
        display: flex;
        margin: 0;
        font-size: 1.4rem;
        margin-bottom: 3rem;
        margin: 1rem 0 1rem 0;
        color: red;
    }

    &__tracks{
        font-size: 1.4rem;
        margin-bottom: 3rem;
    }

    &__track{
        line-height: 0;
        display: flex;
        gap: 3px;
        border-bottom: 2px solid black;
        cursor: pointer;
    }


    &__track:hover{
        color: red;
    }
    

    &__redes{
        font-size: 1.4rem;
        margin-bottom: 50px;
        @include m.tablet{
            margin-bottom: 20px;
        } 
    }

    &__creditos{
        margin-bottom: 2rem;
        font-size: 1.4rem;
    }

    &__creditos p{
        margin: 0;
        margin: 0;
        line-height: 1.2;
        font-size: 1.3rem;
    }


    &__volver{
        margin-bottom: 56px;
        text-align: right;
        @include m.tablet{
            margin-bottom: 30px;
        }  
    }

    &__volver:hover{
        cursor: pointer;
        color: red;
    }

    &__titulo-artista{
        cursor: pointer;
    }

    &__titulo-artista:hover{
        color: red;
    }

    &__track:hover .single__play{
        display: block;
    }

    &__play{
        font-size: 1.3rem;
        display: block;
        line-height: 2.3;
        margin-left: 5%;
        @include m.tabletHorizontal{
            display: none;
            font-size: 1.4rem;
        }
        
        
    }


    &__track [data-lil=""]{
        pointer-events: none;
        opacity: 0.5;
    }


    &__links a[href=""] {
        pointer-events: none;
        cursor: none;
        opacity: 0.5;
    }

}







.arrows-def-single{
    width: 100%;
    justify-content: space-between;
    position: absolute;
    top: 50%;
    display: flex;
    /* margin: 0 2% 0 2%;*/
}


.arrows-def-single #arrow-next{
    margin-right: 5%;
}

.arrows-def-single #arrow-prev{
    margin-left: 5%;
}



       
     