@use 'base/variables' as v;
@use 'base/mixins' as m;



.footer{
    width: 100%;
    border-top: 2px solid;
    position: fixed;
    z-index: 9;
    bottom: 0;
    background-color: white;
    height: 43px;


    &__contenido{
        display: flex;
        align-items: center;
        justify-content: center;
        width: calc(100% - 20px);
        padding: 0 10px 0 10px;
        height: 43px;
    }
}