@use 'base/variables' as v;
@use 'base/mixins' as m;



.family{

    list-style: none;
    min-height: 98vh;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 42px;
    padding-bottom: 41px;


    &__element{
        font-size: 2rem;
        display: flex;
        text-transform: uppercase;
        border-bottom: 2px solid black;
        float: left;
        width: 100%;
    }


/*     &__element:last-of-type{
       padding-bottom: 41px;
    } */

    &__imagen{
        max-width: 100px;
        max-height: 100px;
    }

    &__imagen img{
        position: absolute;
        width: 100px;
        height: auto;
    }


    &__imagen-oculta{
        display: none;
    }

    

    &__contenido{
        font-size: 1.5rem;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: left;
        min-height: 100px;
        width: 65%;
        margin-left: 100px;

        @include m.tablet{
            width: 40%;
        }
    }


    &__cabeza{
        min-width: 100%;
        display: flex;
        padding: 9px 0;
        flex-direction: column;
        align-items: flex-start;
    }

    &__cabeza span{
        padding: 0 10px 13px 5px;
        line-height: 1.8rem;
    }

    &__texto{
        min-width: 100%;
        display: flex;
        padding-bottom: 5px;
        justify-content: space-between;
        padding-left: 6px;
    }

    &__texto a{
        cursor: pointer;
    }

    &__texto a:hover{
        color: red;
    }

    &__texto span{ 
        padding: 0 10px 0 5px;
    }

    &__descripcion{
        display: none;
        margin: 0;
        @include m.tablet{
            display: block;
        }
    }


    &__texto a[href=""] {
        pointer-events: none;
        cursor: none;
        opacity: 0.5;
    }
   

}



.family__imagen:hover .family__imagen-oculta{
    display: block;
}