/* @import url(https://fonts.googleapis.com/css?family=Quattrocento+Sans); */
.loading {
visibility: hidden;
 position: fixed;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 background: white;
 z-index: 9999;
 display: flex;
 flex-direction: column;
 justify-content: center;
 padding-bottom: 65%;
}
@media (orientation: landscape) {
  .loading {
    padding-bottom: 0;
  }
}

@media (min-width: 768px) {
  .loading {
    padding-bottom: 0;
  }
}




.radio.loading{
  visibility: visible;
}


.homeLay.loading{
  visibility: visible;
}


.loading-text {
 right: 0;
 margin: 0 auto;
 text-align: center;
 width: 100%;
 height: 100px;
 line-height: 100px;
}
.loading-text span {
 display: inline-block;
 margin: 0 5px;
 color: black;
 /* font-family: 'Quattrocento Sans', sans-serif; */
}
.loading-text span:nth-child(1) {
 filter: blur(0px);
 animation: blur-text 2.6s 0s infinite linear alternate;
}
.loading-text span:nth-child(2) {
 filter: blur(0px);
 animation: blur-text 2.6s 0.2s infinite linear alternate;
}
.loading-text span:nth-child(3) {
 filter: blur(0px);
 animation: blur-text 2.6s 0.4s infinite linear alternate;
}
.loading-text span:nth-child(4) {
 filter: blur(0px);
 animation: blur-text 2.6s 0.6s infinite linear alternate;
}
.loading-text span:nth-child(5) {
 filter: blur(0px);
 animation: blur-text 2.6s 0.8s infinite linear alternate;
}
.loading-text span:nth-child(6) {
 filter: blur(0px);
 animation: blur-text 2.6s 1s infinite linear alternate;
}
.loading-text span:nth-child(7) {
 filter: blur(0px);
 animation: blur-text 2.6s 1.2s infinite linear alternate;
}
.loading-text span:nth-child(8) {
  filter: blur(0px);
  animation: blur-text 2.6s 1.4s infinite linear alternate;
 }
 .loading-text span:nth-child(9) {
  filter: blur(0px);
  animation: blur-text 2.6s 1.6s infinite linear alternate;
 }
 .loading-text span:nth-child(10) {
  filter: blur(0px);
  animation: blur-text 2.6s 1.8s infinite linear alternate;
 }
 .loading-text span:nth-child(11) {
  filter: blur(0px);
  animation: blur-text 2.6s 2s infinite linear alternate;
 }

 .loading-text span:nth-child(12) {
  filter: blur(0px);
  animation: blur-text 2.6s 2.2s infinite linear alternate;
 }

 .loading-text span:nth-child(13) {
  filter: blur(0px);
  animation: blur-text 2.6s 2.4s infinite linear alternate;
 }

 .loading-text span:nth-child(14) {
  filter: blur(0px);
  animation: blur-text 2.6s 2.6s infinite linear alternate;
 }






@keyframes blur-text {
 0% {
   filter: blur(0px);
}
 100% {
   filter: blur(4px);
}
}



