@use 'base/variables' as v;
@use 'base/mixins' as m;



.radio{

    &__grid{
        margin-bottom: 43px;
        @include m.grid(2, 0); 
        @include m.tablet{
            @include m.grid(3, 0); 
        } 
    }

    &__programa {
        position: relative;
    }

    &__programa img {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
    }

    &__programa img:hover{
        opacity: 0.3;
    }

    &__overlay {
        position: absolute;
        z-index: -1;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        background-color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        text-align: center;
    }

    &__texto{
        text-transform: uppercase;
        color: black;
        font-size: 20px;
    }

    &__botones{
        display: flex;
        justify-content: space-between;
        position: absolute;
        top: 70%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        text-align: center;
        width: 30%;

        
    }

    &__botones-left:hover{
        color: red;
    }
    
}





.overlay-active {
    filter: opacity(0.5);
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}


.overlayPlayActive{
    color: red;
}