@use 'base/variables' as v;
@use 'base/mixins' as m;



.header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    background-color: white;
    z-index: 20;
   

    &__contenedor{
        justify-content: space-between;
        display: flex;
        align-items: center;
        width: 100%;
        border-bottom: 2px solid;
    }

    &__navegacion-redes{
        display: none;

        @include m.tabletHorizontal{
            filter: opacity(0);
            width: 50%;
            display: flex;
            justify-content: space-around;
            transition: all 0.2s ease;
            padding-left: 10px;
            height: 43px;
            align-items: center;
        }

        @include m.xl_desktop{
            width: 60%;
        }

        &:hover{
            filter: opacity(1);
        }   
    }

    &__enlace{
        text-decoration: none;
        color: black;

        &:hover{
            color: red;
        }  
    }

    &__contenido{
        padding-bottom: 3px;
        display: flex;
        cursor: pointer;
        align-items: flex-end;
        height: 46px;
        padding-left: 10px;
        width: 100%;
        border-left: 0px solid;
        font-size: 3.8rem;

        @include m.tabletHorizontal{
            width: 50%;
            border-left: 2px solid;
        }

        @include m.xl_desktop{
            width: 40%;
        }

        &:hover{
            background: black;
            border-color: black;
            color: white;
        }

         &:hover .logo img{
            filter: invert(0);
        } 
    }

    
    &__logo img{
        display: none;
        margin-top: 10%;
        width: 40px; 
        @include m.telefono{
            display: block;
            font-size: 3rem;
        }

    }




    &__titulo{
        font-size: 3.5rem;
        width: 100%;
        margin: 0;
        font-size: 25px;
        padding: 0 10px 0 10px;

        @include m.tablet{
            font-size: 3rem;
        }

        @include m.xl_desktop{
            font-size: 3.8rem;
        }
    }

    &__navegacion-principal{
        display: flex;
        width: 100%;
        right: -100%;
        position: fixed;
        top: 48px;
        height: calc(100vh - 90px);
        background-color: black;
        color: white;
        text-transform: uppercase;
        flex-direction: column;
        text-align: right;
        align-items: center;
        justify-content: center;
        z-index: 25;
        padding-left: 0px;
        transition: all 0.2s ease;


        @include m.xl_desktop{
            width: 40%;
            /* padding-left: 2px; */
        }
    }

    &__navegacion-principal p{
        margin: 0;
        color: white;
        font-size: 4rem;
        border-bottom: 2px solid transparent;
        cursor: pointer;
    }

    &__navegacion-principal p:hover{
        color: red;
    }

    &__navegacion-principal a{
        border-bottom: 2px solid transparent;
        cursor: pointer;
        text-align: center;
        color: white;
        font-size: 3rem;
        margin: 0;
        
        @include m.miniTelefono{
            font-size: 4rem;

        }
    }
    
    @media (orientation: landscape) {
        &__navegacion-principal a{
            font-size: 3rem;
            @include m.tabletHorizontal{
                font-size: 4rem;
            }
        }
    }
    

    &__navegacion-principal a:last-of-type{
        margin-top: 2%;
        text-align: center;
    }
    
    
    &__navegacion-principal a:hover{
        color: red;
    }

    &__copy{
        position: absolute;
        bottom: 15px;
        text-align: center;
        font-size: 1.6rem;
    }

    @media (orientation: landscape) {
        &__copy{
            bottom: 2px;
            font-size: 1.3rem;
            @include m.tabletHorizontal{
                bottom: 15px;
                font-size: 1.6rem;
            }
        }
    }
}




.header__contenido:hover .header__logo img {
    filter: invert(1);
}



/*cartela home*/

.cartela {
    height: 43px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    width: 100%;
    border-bottom: 2px solid;
    position: fixed;
    top: 43px;
    z-index: 19;
    background-color: white;
}


.cartela a {
    color: black;
    margin-top: 0.25%;
}

.cartela a:hover {
    color: red;
}


.scrolling {
    padding-left: 20px;
    position: relative;
    -webkit-animation: scroll 60s infinite linear;
    -moz-animation: scroll 60s infinite linear;
    -o-animation: scroll 60s infinite linear;
    animation: scroll 60s infinite linear;
    @include m.tablet{
        padding-left: 350px;
    }
}

@keyframes scroll {
    0% {
        left: 500px;
    }
    100% {
        left: -1800px;
    }
}

@-webkit-keyframes scroll {
    0% {
        left: 500px;
    }
    100% {
        left: -1800px;
    }
}

@-moz-keyframes scroll {
    0% {
        left: 500px;
    }
    100% {
        left: -1800px;
    }
}

@-ms-keyframes scroll {
    0% {
        left: 500px;
    }
    100% {
        left: -1800px;
    }
}

@-o-keyframes scroll {
    0% {
        left: 500px;
    }
    100% {
        left: -1800px;
    }
}