@use 'base/variables' as v;
@use 'base/mixins' as m;


.streaming-soundcloud {
    display: flex;
    height: 43px;
    align-items: center;
    justify-content: space-between;
    width: calc(100% - 20px);
    padding: 0 10px 0 10px;
    border-top: 2px solid;
    position: fixed;
    z-index: 9;
    bottom: 0;
    background-color: white;
    /* display: none; */
}

.s-title {
    font-size: 1.3rem;
}

@media(min-width:900px) {
    .s-title {
        font-size: 1.6rem;
    }
}

#duration-l {
    font-size: 1.3rem;
    margin-left: 10px;
}

@media(min-width:768px) {
    #duration-l {
        font-size: 1.6rem;
    }
}

.s-duration {
    display: flex;
    align-items: center;
}

.s-buttons {
    padding-right: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
}

.s-buttons svg {
    width: 25px;
    height: 25px;
}

@media(min-width:768px) {
    .s-buttons svg {
        width: 30px;
        height: 30px;
    }
}

#pause svg {
    height: 25px;
}

#pause-l.active svg {
    fill: red;
}

#play-l.active svg {
    fill: red;
}

#pause-l:hover svg {
    fill: red;
}

#play-l:hover svg {
    fill: red;
}

#pause-r.active svg {
    fill: red;
}

#play-r.active svg {
    fill: red;
}

#pause-r:hover svg {
    fill: red;
}

#play-r:hover svg {
    fill: red;
}


iframe {
     display: none; 
}

#rangeslider-r::-webkit-slider-thumb,
#rangeslider-l::-webkit-slider-thumb {
    display: none;
}

#rangeslider-r::-moz-range-thumb,
#rangeslider-l::-moz-range-thumb {
    display: none;
}

#rangeslider-r,
#rangeslider-l {
    display: none;
}

@media(min-width:768px) {
    #rangeslider-r::-webkit-slider-thumb,
    #rangeslider-l::-webkit-slider-thumb {
        display: block;
        -webkit-appearance: none;
        appearance: none;
        width: 15px;
        height: 15px;
        border: 1px solid black;
        background: red;
    }
    #rangeslider-r::-moz-range-thumb,
    #rangeslider-l::-moz-range-thumb {
        display: block;
        width: 15px;
        height: 15px;
        border: 1px solid black;
        background: red;
    }
    #rangeslider-r,
    #rangeslider-l {
        display: block;
        -webkit-appearance: none;
        background-color: black;
        height: 6px;
        cursor: pointer;
        margin: 0 15px 0 15px;
    }
}

#prev-l,
#next-l {
    cursor: pointer;
}

#prev-l svg,
#next-l svg {
    width: 25px;
}

@media(min-width:768px) {
    #prev-l svg,
    #next-l svg {
        width: 35px;
    }
}

#prev-l:hover path,
#next-l:hover path {
    fill: red;
}

#next-l {
    margin-left: 15px;
}

.copy {
    position: absolute;
    bottom: 15px;
    text-align: center;
}


.ocultar-prev{
    display: none;
}




.streaming-radio{
    display: flex;
    position: fixed;
    bottom: 0;
    background-color: white;
    justify-content: space-between;
    width: 100%;

}


.s-buttonsRadio {
    padding-right: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    margin-left: 2%;
}

.s-buttonsRadio svg {
    width: 25px;
    height: 25px;
}


#durationRadio {
    font-size: 1.3rem;
    margin-left: 10px;
}

@media(min-width:768px) {
    #durationRadio {
        font-size: 1.6rem;
    }
}

.s-durationRadio {
    display: flex;
    align-items: center;
}

