@use 'base/variables' as v;
@use 'base/mixins' as m;


/*slider*/
.center_y{ 
    position: relative;
    top: 75%;
    transform: translateY(100%);
    @include m.tablet{
        top: 50%;
        transform: translateY(-50%);
    }
}

.center_x{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    
}

#slider-container{
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.slide{
    width: 100%;
    height: 100%;
    
}

.slide img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: opacity 0.6s ease;
}

.slide{
    position: absolute;
}


#dots-wrapper{
    display: flex;
    position: absolute;
    bottom: 30px;
}

.dot-navigation{
    width: 15px;
    height: 15px;
    border-radius: 100%;
    cursor: pointer;
    margin: 0 4px;
    border: 2px solid whitesmoke;
    transition: .5s background-color;
    display: none;
}


/* fade animation */
.fade{
    animation-name: fade;
    animation-duration: .5s;
}
@keyframes fade{
    from {opacity: .2}
    to {opacity: 1}
}










@media(min-width:768px) {
    .texto-artista {
        margin-left: 25%;
    }
}

@media(min-width:1024px) {
    .texto-artista {
        margin-left: 20%;
    }
}


.artista {

   &__container .arrows-def{
        height: 88%;
        width: 100%;
        position: absolute;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        left: -5px;
        z-index: 0;
    
    
        @include m.tablet{
        width: 16%;
        justify-content: space-around;
        }   
    }


    &__container .slide{
        
    }

    &__container .slider-arrow {
        position: fixed;
    }
    

    &__container .slider-arrow {
        color: black;
        cursor: pointer;
        font-size: 1rem;
    }
    
    &__container #arrow-prev{
        z-index: 99;
        left: 20px;
        font-size: 1.6rem;
        text-shadow: 1px 0px 6px #17171782;
        @include m.tablet{
            padding: 6% 2%;
        }
    }


    


    
    &__container #arrow-next{
        z-index: 99;
        right: 20px;
        font-size: 1.6rem;
        text-shadow: 1px 0px 6px #17171782;
        width: 50px;

        @include m.tablet{
            left: 100px;
            padding: 6% 2%;
        }
    }
    
    &__container #arrow-prev:hover{
        text-shadow: -10px 0px 10px #17171782;
    }
    
    #arrow-next:hover{
        text-shadow: 10px 0px 10px #17171782;
    }


    
    &__texto{
        margin-top: 0;
        margin-left: 0%;
        position: fixed;
        z-index: 9;
        display: none;
        line-height: 1.5;
        color: black;
        text-decoration: none;
        opacity: 0.8;
        padding: 0 15px;
        overflow-y: overlay;
        width: 100%;
        /* overflow-y: scroll; */
        height: 100%;

        @include m.tablet {
            margin-left: 25%;
        }

        @include m.tablet {
            margin-left: 20%;
        }
    }

    &__texto h2{
        font-size: 3.2rem;
        margin: 0;
    }



    &__descripcion{
        text-align: left;
        color: black;
        margin-bottom: 1rem;
    }

    &__tags{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: red;
    }

    &__tags p{
        margin-right: 2rem;
    }

    &__releases{
        padding-left: 0;
        width: 95%;
        margin-bottom: 3rem;
        @include m.tablet {
            width: 79%;
        }
    }

    &__release{
        border-bottom: 2px solid black;
        list-style-type: none; 
        padding: 2px 0;
        font-size: 1.3rem;
        cursor: pointer;
    }

    &__release:hover{
        color: red;
    }

    &__links {
        border-bottom: 2px solid black;
        border-top: 2px solid black;
        width: 95%;
        display: flex;
        justify-content: space-between;
        padding: 2px 0;
        @include m.tablet {
            width: 79%;
        }
    }

    &__links a{ 
        color: black;
        font-size: 1.4rem;
    }

    &__links a:hover{ 
        color: red;
    }
    

    &__volver{
        position: fixed;
        bottom: 10%;
        right: 2%;
        z-index: 99;
    }


    &__volver:hover{
        cursor: pointer;
        color: red;
    }


    /* &__tracks{
        font-size: 1.7rem;
        margin-bottom: 1rem;
    } */


    &__links a[href=""] {
        pointer-events: none;
        cursor: none;
        opacity: 0.5;
    }

  /*   .class_name img[src=""] {
        display: none;
    } */

    &__titulo-des{
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }

    &__titulo-des p{
        color: red;
        position: fixed;
        cursor: pointer;
        margin: 0;
        padding-right: 20px;
        font-size: 3rem;
        right: 6%;
        @include m.tabletHorizontal {
            display: none;
        }
    }

    &__redes{
        margin-bottom: 150px;
        @include m.tabletHorizontal {
            margin-bottom: 30px;
        }
    }



}


#arrow-minim{
    left: 37%;
    padding: 6% 2%;
    font-size: 1.6rem;
    text-shadow: 1px 0px 6px rgb(23 23 23 / 51%);
    @include m.tablet {
        display: none;
    }
}















  
 
  
.slide:hover .artista__texto{
    @include m.tablet {
        display: block;
    } 
}


.slide:hover .image-slider{  
    @include m.tablet {
        transition: opacity 0.6s ease;
        opacity: 0.6; 
    } 
}



.slider-container{
    position: relative;
    height: 88vh;
    overflow: hidden;
}   

.image-slider{
    position: relative;
    height: 93vh;
}



.flash{  
        transition: opacity 0.6s ease;
        opacity: 0.6; 
}





@media(orientation: landscape) {
    .artista__container #arrow-prev {
        top: 45%;
    }
}

@media(orientation: landscape) {
    .artista__container #arrow-next {
      top: 45%;
    }
}
