@use 'variables' as v;
@use 'mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.8rem;
}
a {
    text-decoration: none;
    color: black;
}
img {
    width: 100%;
    height: auto;
}


.mostrar {
    display: flex !important;
    -moz-display: flex;
    -webkit-display: flex;
    -o-display: flex;
    -ms-display: flex;
  }

  .mostrar2 {
    display: block!important;
  }



  .mostrar__menu{
    right: 0 !important;
  }

  
.ocultar {
    display: flex !important;
    -moz-display: flex;
    -webkit-display: flex;
    -o-display: flex;
    -ms-display: flex;
  }

  .ocultar2{
    display: none !important;
  }


  .margen-largo{
    margin-top: 86px;
  }

  .margen-corto{
    margin-top: 48px !important;
  }



  input[type="range"]{
    display: block;
    position: relative;
    width: 100%;
    padding: 0;
    margin: 0;
    background: transparent;
    overflow: hidden;
    outline: none;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    -ms-box-shadow: none;
    -o-box-shadow: none;
    box-shadow: none;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    text-align: inherit;
    color: inherit;
    border: none;
}


.disabled{
  pointer-events: none;
  opacity: 0.5;
}