@use 'base/variables' as v;
@use 'base/mixins' as m;


.ventana__principal{
    margin-top: 86px;
}

.home{

    &__lista{
        margin-top: 3px;  
        border-right: 0;
        @include m.tablet{
            margin-top: 0;
            border-right: 0px solid black;
            @media (orientation: landscape) {
                height: 88vh;
                max-width: 65%;
            }
        }
    }

    &__movWrap{ 

        margin-bottom: -4px;
        @include m.tablet{
            display: none;
        }
    }


    &__release{
        border-bottom: 2px solid black;
        text-transform: uppercase;
        filter: blur(0.1px);
        font-size: 1.4rem;
        cursor: pointer;

        @include m.tablet{
            font-size: 1.9rem;
        }
    
        @include m.xl_desktop{
            font-size: 3rem;
        }
    }

    &__release:first-of-type{
        border-top: 2px solid #252525;
        @include m.tablet{
            border-top: 0;
        }
    }

    &__release:last-of-type{
        text-align: center;
    }

    &__release:last-of-type p{
        margin: 0.5rem;
    }

    &__release:last-of-type p:hover{
        color:red
    }

    &__last{
        border-bottom: 2px solid black;
        text-transform: uppercase;
        filter: blur(0.1px);
        font-size: 1.4rem;
        text-align: center;
        padding: 0.5rem 0;

        @include m.tablet{
            font-size: 1.9rem;
            border-right: 2px solid black;
        }

        @include m.xl_desktop{
            font-size: 3rem;
        }
    }    

    &__last a:hover{
        color:red;  
        cursor: pointer;
    }

    &__last a{
        margin: 0.5rem;
    }

    &__release a{
        display: flex; 
    }


    &__release a:hover > .home__left{
        color: red;
        width: 58%;
    }

    &__release div:hover > .home__right{
        filter: blur(0.5px);
    }

    &__release div:hover > .home__right span:last-child{
        display: block;
    }

    &__right a{
        padding-left: 3%;
    }
    

    &__left{
        margin-left: 2%;
        width: 60%;
        border-right: 2px solid black;
        padding: 0.5rem 0;
    }

    &__right span:first-child{
        margin-left: 2%;
    }

    &__right span:last-child{
        margin-right: 2%;
        display: none;
    }

    &__right{
        display: flex;
        width: 40%;
        justify-content: space-between;
        padding: 0.5rem 0;
        @include m.tablet{
        border-right: 2px solid black;
    }
}

    &__canvas{
        display: none;
        width: calc(35% - 2px);
        height: calc(100vh - 95px);
        position: fixed;
        z-index: -99;
        right: 0;
        top: 48px;
        @include m.tablet{
            display: block
        }
    }

    &__canvasElement{
        display: none;
        justify-content: space-around;
    }

    &__canvasElement img{
        width: 80%;
        position: absolute;
        top: 15%;
    }

    &__corona{
        display: block;
        @include m.tablet{
            display: none;
        }
    }


    &__tituloSlide{
        position: absolute;
        /* z-index: 99; */
        color: black;
        width: 100%;
        top: 20px;
        left: 2%;
    }

    &__tituloSlideDos{
        position: absolute;
        /* z-index: 99; */
        color: black;
        width: 100%;
        top: 45px;
        left: 2%;
    }

    &__footerMov{
        margin-bottom: 44px;
        padding: 0.3rem;
        /* position: absolute; */
        /* bottom: 27px; */
        border-top: 2px solid;
        width: 100%;
        display: flex;
        justify-content: space-around;
        @include m.tablet{
            display: none;
        }
    }

    &__footerMov a:hover{
        color: red;
    }


    /* &__movWrap .slide img{
        object-fit: scale-down;
    } */

    &__movWrap .slider-container{
        height: 45vh;
    }


    &__canvasImg{
        width: 80%;
        position: absolute;
        top: 15%;
        left: 10%;
    }
}


















