@use 'variables' as v;
@use 'mixins' as m;

body{
    font-family: 'swiss_721roman', 'sans-serif';
    font-weight: bold;
    font-size: 1.6rem;
}

h1, h2, h3 {
    @include m.headings;
    font-family: 'swiss_721roman', 'sans-serif';
    font-weight: bold;
    font-size: 1.6rem;
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
.text-center {
    text-align: center;
}

[class$="__heading"]{ //a esta movida se le llama selectores de clase, echarle un ojo
    @include m.nombre-pagina;
}


[class$="__descripcion"]{ //a esta movida se le llama selectores de clase, echarle un ojo
    @include m.descripcion-pagina;
}