@use 'base/variables' as v;
@use 'base/mixins' as m;


.releases{

    &__table{
        display: none;
        margin-bottom: 41px;
        width: 100%;
            border-spacing: 0px;
            position: sticky;
        @include m.tablet{
            display: inline-table;
            
        }
    }


    &__table th{
        position: sticky;
        border-bottom: 2px solid black;
        text-align: center;
        width: 20%;
        height: 30px;
        text-transform: uppercase;
    }


    &__table thead{
        position: fixed;
        top: 85px;
        display: flex;
        width: 100%;
    }

    &__table thead th:first-of-type{
        width: 5%;
    }

    &__table td{
        border-bottom: 2px solid black;
        width: 20%;
        text-align: center;
        height: 30px;
    }

    &__table tbody td:first-of-type{
        width: 5%;
    }

    &__icono{
        right: 5%;
        z-index: 99;
        margin-left: 15px;
        position: fixed;
        margin-top: 5px;
        width: 25px;
        @include m.tablet{
            left: 0;
        }
    }

    &__icono i:hover{
        color: red;
    }
    

    &__iconoLista{
        right: 5%;
        z-index: 99;
        margin-left: 15px;
        position: fixed;
        margin-top: 5px;
        @include m.tablet{
            left: 0;
        }
    }


    &__iconoLista i:hover{
        color: red;
    }

    &__grid{
        @include m.grid(2, 0); 
        @include m.tablet{
            @include m.grid(3, 0); 
        } 
    }


    &__gridElement{
        position: relative;
        cursor: pointer;
    }

    &__gridElement img{
        width: 100%;
        height: 100%;
    }

    &__gridOverlay{
        position: absolute;
        z-index: -1;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        background-color: white;

        z-index: 8;
        line-height: 1.5;
        color: black;
        text-decoration: none;
        opacity: 0.8;

        text-align: center;
        display: none;
    }

    &__gridHead{
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        padding: 0;
        font-size: 1.8rem;
        @include m.tablet{
            padding: 15% 0;
        } 
    }
    


    &__gridText{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        padding: 10% 0;
        @include m.tablet{
            flex-direction: row; 
        } 
    }

    &__gridText p{
        margin: 0;
    }



    &__gridText a:hover{
        color: red;
    }

    &__gridText p:hover{
        color: red;
    }


    &__tituloTabla a:hover {
        color: red;
        cursor: pointer;
    }

    &__artistatituloTabla a:hover{
        color: red;
        cursor: pointer;
    }


    &__release{
        width: 100%;
        display: flex;
    }

    &__release td{
        padding-top: 5px;
    }

    &__tbody{
        top: 15px;
        position: absolute;
        width: 100%;
        padding-bottom: 41px;
    }

    &__table-head th{
        padding-top: 6px;
        font-size: 1.7rem;
    }

    &__table-head{
        background-color: white;
        z-index: 99;
    }

    &__playlist{
        margin: 0;
    }


    &__play{
        opacity: 0.8;
        cursor: pointer;
    }

    &__play:hover{
        color: red;
    }

    &__play[data-lil=""]{
        pointer-events: none;
        opacity: 0.1;
    }


 
}










.releases__gridElement:hover .releases__gridOverlay{
    display: block;
}




.releaseLista{

    list-style: none;
    min-height: 98vh;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 85px;
    @include m.tablet{
        display: none;
    }

    &__element{
        font-size: 2rem;
        display: flex;
        text-transform: uppercase;
        border-bottom: 2px solid black;
        float: left;
        width: 100%;
    }


    &__element:last-of-type{
       padding-bottom: 41px;
    }

    &__imagen{
        max-width: 100px;
        max-height: 100px;
    }

    &__contenido{
        width: 75%;
        font-size: 1.5rem;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: left;
        min-height: 100px;
    }


    &__cabeza{
        min-width: 100%;
        display: flex;
        padding: 9px 0;
        flex-direction: column;
        align-items: flex-start;
    }

    &__cabeza a{
        padding: 0 10px 0 5px;
        line-height: 1.8rem;
    }

    &__artista:hover{
        color: red;
    }

    &__titulo:hover{
        color: red;
    }

    &__texto{
        min-width: 100%;
        display: flex;
        justify-content: space-between;
        padding-bottom: 5px;
    }

    &__texto span{ 
        /* padding: 0 10px 0 5px; */
    }


    &__play {
        padding-right: 2%;
        font-size: 1.3rem;
    }

   
}


.releasePlaying{
    color: red;
}

.releasePlayerMain{
    fill: red;
}


.releaseStopMain{
    fill: black;
}